import { GetLocation } from '../../rewriters/helper/getLocation';

window.eval = new Proxy(window.eval, {
  apply(
    target: typeof eval,
    thisArg: any,
    argArray: Parameters<typeof eval>,
  ): ReturnType<typeof eval> {
    if (argArray[0]) {
      argArray[0] = self.__envade$rewrite.javascript(argArray[0], GetLocation());
    }

    return Reflect.apply(target, thisArg, argArray);
  },
});

window.Function = new Proxy(Function, {
  construct(target: FunctionConstructor, argArray: string[]): Function {
    const lastIndex = argArray.length - 1;
    if (argArray[lastIndex]) {
      argArray[lastIndex] = self.__envade$rewrite.javascript(argArray[lastIndex], GetLocation());
    }

    return Reflect.construct(target, argArray);
  },
});
