const urlProps = new Set([
  'background',
  'background-image',
  'mask',
  'mask-image',
  'list-style',
  'list-style-image',
  'border-image',
  'border-image-source',
  'cursor',
]);

const curUrl = new URL(self.scope$location.origin);

const originalSetProperty = CSSStyleDeclaration.prototype.setProperty;

CSSStyleDeclaration.prototype.setProperty = new Proxy(originalSetProperty, {
  apply(target, thisArg, args) {
    const [propertyName, value, priority] = args;

    if (urlProps.has(propertyName)) {
      const rewrittenValue = self.__envade$rewrite.css(value, curUrl);
      return Reflect.apply(target, thisArg, [propertyName, rewrittenValue, priority]);
    }

    return Reflect.apply(target, thisArg, args);
  },
});
