import { GetLocation } from "../../rewriters/helper/getLocation";

Object.defineProperty(document, 'URL', {
  get() {
    return GetLocation().href
  },
  configurable: true
});

Object.defineProperty(document, 'baseURI', {
  get() {
    return GetLocation().href;
  },
  configurable: true
});

Object.defineProperty(document, 'documentURI', {
  get() {
    return GetLocation().href;
  },
  configurable: true
});


