import { GetLocation } from '../../rewriters/helper/getLocation';

window.history.pushState = new Proxy(window.history.pushState, {
  apply(target, thisArg, argArray) {
    if (argArray[2]) {
      argArray[2] = self.__envade$rewrite.url.rewriteUrl(argArray[2], GetLocation().href);
    }

    return Reflect.apply(target, thisArg, argArray);
  },
});

