import { GetLocation } from '../rewriters/helper/getLocation';

const proxy = {
  get href() {
    return GetLocation().href;
  },
  set href(url: string) {
    if (url.startsWith(location.origin)) {
      url = url.slice(location.origin.length);
      console.log('Rewriting href (url split)', url);
      location.href = self.__envade$rewrite.url.rewriteUrl(url, GetLocation().href);
      return;
    }
    console.log('Setting href (full url)', url);
    location.href = self.__envade$rewrite.url.rewriteUrl(url, GetLocation().href);
  },
  get origin() {
    return GetLocation().origin;
  },
  get protocol() {
    return GetLocation().protocol;
  },
  set protocol(_) {},
  get host() {
    return GetLocation().host;
  },
  set host(value: string) {
    const url = GetLocation();
    url.host = value;
    window.location.href = self.__envade$rewrite.url.rewriteUrl(url.toString(), url);
  },
  get hostname() {
    return GetLocation().hostname;
  },
  set hostname(value: string) {
    const url = GetLocation();
    url.hostname = value;
    window.location.href = self.__envade$rewrite.url.rewriteUrl(url.toString(), url);
  },
  get port() {
    return GetLocation().port;
  },
  set port(_) {},
  get pathname() {
    return GetLocation().pathname;
  },
  set pathname(val: string) {
    const url = GetLocation();
    url.pathname = val;
    window.location.href = self.__envade$rewrite.url.rewriteUrl(url.toString(), url);
  },
  get search() {
    return GetLocation().search;
  },
  set search(val: string) {
    const url = GetLocation();
    url.search = val;
    window.location.href = self.__envade$rewrite.url.rewriteUrl(url.toString(), url);
  },
  get hash() {
    return GetLocation().hash;
  },
  set hash(val: string) {
    const url = GetLocation();
    url.hash = val;
    window.location.href = self.__envade$rewrite.url.rewriteUrl(url.toString(), url);
  },
  toString() {
    return GetLocation().toString();
  },
  assign(url: string) {
    location.assign(self.__envade$rewrite.url.rewriteUrl(url, GetLocation().href));
  },
  replace(url: string) {
    location.replace(self.__envade$rewrite.url.rewriteUrl(url, GetLocation().href));
  },
  reload() {
    location.reload();
  },
};

Object.defineProperty(window, 'scope$location', {
  configurable: true,
  enumerable: true,
  get() {
    return proxy;
  },
  set(_) {},
});

Object.defineProperty(globalThis, 'scope$location', {
  configurable: true,
  enumerable: true,
  get() {
    return proxy;
  },
  set(_) {},
});

Object.defineProperty(document, 'scope$location', {
  configurable: true,
  enumerable: true,
  get() {
    return proxy;
  },
  set(_) {},
});

